import React, { useState } from 'react'
import {
  Container,
  Button,
  TextField,
  Typography,
  Grid,
  Box,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@mui/material'
import { useNavigate } from 'react-router-dom'
import emailjs from 'emailjs-com'
import { Lorem } from '../Constants/constants'
import { Header } from '../Components/Header'

emailjs.init('user_8HEIx6CnEORghk_fMdGcv')

export const FrontPage = () => {
  const [email, setEmail] = useState('')
  const [dialogOpen, setDialogOpen] = useState(false)
  const [dialogMessage, setDialogMessage] = useState('')
  const [dialogTitle, setDialogTitle] = useState('')
  const navigate = useNavigate()

  const handleEmailChange = (event) => {
    setEmail(event.target.value)
  }

  const handleLogin = () => {
    navigate('/login')
  }

  const handleRequestDemo = () => {
    const templateParams = {
      to_email: 'kristiankassoemeier@gmail.com',
      customer_email: email,
    }

    emailjs
      .send(
        'service_qeoz2v8',
        'template_jyhtxjp',
        templateParams,
        'vewCrKxqS5W_lq7BU'
      )
      .then(
        (response) => {
          console.log('Email successfully sent!', response)
          setDialogTitle('Tjek din email')
          setDialogMessage('Vi har sendt loginoplysninger til dig på din email')
          setDialogOpen(true)
        },
        (error) => {
          console.log('Failed to send email:', error)
          setDialogTitle('Der opstod en fejl')
          setDialogMessage('Kontakt os på test@test.dk')
          setDialogOpen(true)
        }
      )
  }

  const handleCloseDialog = () => {
    setDialogOpen(false)
    setEmail('')
  }

  return (
    <Container
      maxWidth="lg"
      style={{ marginTop: '50px' }}>
      <Header content="Udlægshåndtering gjort simpelt" />
      <Grid
        container
        spacing={3}>
        <Grid
          item
          xs={12}
          md={6}
          style={{ padding: '20px' }}>
          <Grid
            container
            spacing={3}
            alignItems="center">
            <Grid
              item
              xs={12}>
              <Typography variant="body2">{Lorem}</Typography>
            </Grid>
            <Grid
              item
              xs={12}>
              <Button
                variant="contained"
                color="primary"
                fullWidth
                onClick={handleLogin}
                style={{ height: '56px' }}>
                Log Ind
              </Button>
            </Grid>
            <Grid
              item
              xs={12}>
              <Typography variant="body2">{Lorem}</Typography>
            </Grid>
            <Grid
              item
              xs={8}>
              <TextField
                label="Email"
                variant="outlined"
                fullWidth
                value={email}
                onChange={handleEmailChange}
                style={{ height: '56px' }}
              />
            </Grid>
            <Grid
              item
              xs={4}>
              <Button
                variant="contained"
                color="secondary"
                fullWidth
                onClick={handleRequestDemo}
                style={{ height: '56px' }}>
                Anmod om Demo
              </Button>
            </Grid>
          </Grid>
        </Grid>
        <Grid
          item
          xs={12}
          md={6}
          style={{ height: '100%', padding: '20px' }}>
          <Box
            sx={{
              width: '100%',
              position: 'relative',
              paddingTop: '56.25%',
            }}>
            <iframe
              src="https://www.youtube.com/embed/jQbbvh_rphI"
              allowFullScreen
              title="Embedded youtube"
              style={{
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
              }}
            />
          </Box>
        </Grid>
      </Grid>
      <Dialog
        open={dialogOpen}
        onClose={handleCloseDialog}>
        <DialogTitle>{dialogTitle}</DialogTitle>
        <DialogContent>
          <DialogContentText>{dialogMessage}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleCloseDialog}
            color="primary"
            autoFocus>
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  )
}
