import {
  Container,
  TextField,
  Button,
  Grid,
  Dialog,
  DialogTitle,
} from '@mui/material'
import { UploadButton } from '../Components/UploadButton'
import { useAppContext } from '../Context/AppContext'
import { Header } from '../Components/Header'
import { NavLink } from '../Components/NavLink'
import { AccountMenu } from '../Components/AccountMenu'
import { DemoButton } from '../Components/DemoButton'

export const AppPage = () => {
  const {
    handleSubmit,
    isModalOpen,
    handleDateChange,
    handleDescriptionChange,
    date,
    amount,
    description,
    handleModalClose,
    isNotNumber,
    handleAmountChange,
  } = useAppContext()

  return (
    <Container
      maxWidth="sm"
      sx={{ position: 'relative' }}>
      <AccountMenu />
      <Header content="Registrer udlæg" />
      <form onSubmit={handleSubmit}>
        <Grid
          container
          spacing={3}>
          <Grid
            item
            xs={12}>
            <TextField
              fullWidth
              label="Bilagsdato"
              type="date"
              value={date}
              onChange={handleDateChange}
              InputLabelProps={{
                shrink: true,
              }}
            />
          </Grid>
          <Grid
            item
            xs={12}>
            <TextField
              fullWidth
              required
              type="number"
              label="Beløb i DKK"
              value={amount}
              onChange={handleAmountChange}
              error={isNotNumber}
              helperText={isNotNumber ? 'Indtast venligst et tal' : ''}
            />
          </Grid>
          <Grid
            item
            xs={12}>
            <TextField
              fullWidth
              label="Kommentar"
              value={description}
              onChange={handleDescriptionChange}
            />
          </Grid>
          <Grid
            item
            xs={12}>
            <UploadButton />
          </Grid>
          <Grid
            item
            xs={12}>
            <Button
              variant="contained"
              color="primary"
              type="submit">
              Registrer
            </Button>
          </Grid>
        </Grid>
      </form>
      <NavLink
        to="/history"
        text="Udlægsregistreringer"
      />
      <Dialog
        open={isModalOpen}
        onClose={handleModalClose}>
        <DialogTitle>Dit udlæg er registreret</DialogTitle>
        <Button onClick={handleModalClose}>Luk</Button>
      </Dialog>
    </Container>
  )
}
