import { useEffect } from 'react'
import {
  Container,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Box,
} from '@mui/material'
import { NavLink } from '../Components/NavLink'
import { Header } from '../Components/Header'
import { API_ENDPOINT, RECEIPT_HEADERS } from '../Constants/constants'
import { useFetchReceipts } from '../Hooks/useFetchReceipts'

export const HistoryPage = () => {
  const { fetchReceipts, receipts } = useFetchReceipts()

  useEffect(() => {
    fetchReceipts()
  }, [fetchReceipts])

  return (
    <Container maxWidth="sm">
      <Header content="Registreringshistorik" />
      <Grid
        item
        xs={12}>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                {RECEIPT_HEADERS.map((header) => (
                  <TableCell key={header}>{header}</TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {receipts.map((receipt) => (
                <TableRow key={receipt.receipt}>
                  <TableCell>{receipt.date}</TableCell>
                  <TableCell>{receipt.amount}</TableCell>
                  <TableCell>{receipt.description}</TableCell>
                  <TableCell>
                    <Box
                      component="img"
                      src={`${API_ENDPOINT}${receipt.receipt}`}
                      alt="Receipt Thumbnail"
                      sx={{ width: 50, height: 50 }}
                    />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
      <NavLink
        to="/app"
        text="Registrer udlæg"
      />
    </Container>
  )
}
